






















import {Vue, Component} from 'vue-property-decorator';
@Component({
    props: {
        cards: {
            type: Array,
            required: true,
        },
    },
})
export default class CardListH5 extends Vue {
    public updated(): void {
        setTimeout(() => {
            const $backs: any = document.getElementsByClassName('back');
            const holder: any[] = [];
            for (const v of $backs) {
                holder.push(v);
            }
            for (const v of holder) {
                const $pa = v!.parentElement;
                $pa!.removeChild(v);
            }
        }, 250);
        const $cards = document.getElementsByClassName('branker')[0]
                .getElementsByClassName('cards')[0] as HTMLElement;
        if ((this as any).cards.length < 2) {
            $cards!.style!.transform = 'translateX(0)';
        } else {
            $cards!.style!.transform = 'translateX(-' + (0.18 * ((this as any).cards.length - 2)) + 'rem)';
        }
    }
}
