





















































































































































































































































import {Component, Vue} from 'vue-property-decorator';
import {BlackjackController} from '../../modules/contract/blackjack/blackjackController';
import HistoryTableH5 from '@/views/blackjack-h5/blackjackHistoryTableH5.vue';
import CardListH5 from '@/components/cardListH5.vue';
import BetViewH5 from '@/components/BetViewH5.vue';
import Toast from '@/components/toast.vue';
import {EOS} from '../../modules/eos/eos';
import {Scatter} from '@/modules/eos/scatter';
import {Constants} from '@/modules/eos/constants';
import {BlackjackResult} from '@/modules/contract/blackjack/blackjackGame';
@Component({
    components: {
        BetViewH5,
        HistoryTableH5,
        CardListH5,
        Toast,
    },
})
export default class BlackjackView extends Vue {
    public readonly control: BlackjackController = new BlackjackController(EOS.instance);
    public showHis: boolean = false;
    public showHelp: boolean = false;
    public showSet: boolean = false;
    public showInvite: boolean = false;
    public showRule: boolean = false;
    public musicOn: boolean = true;
    public langCn: boolean = false;
    public copySuc: boolean = false;
    public isLogin: boolean = !this.control.loggedIn();
    public tipsTxt: string = '';
    public links: string = 'https://godapp.github.io/#/blackjack-h5';

    public mounted(): void {
        this.control.initialize();
        const $bgm: any = document.getElementById('bgm');
        $bgm.play();
        document.addEventListener('WeixinJSBridgeReady', () => {
            $bgm.play();
        }, false);
    }

    public get cpuPer(): string {
        const obj = this.control.accountControl.accountInfo.cpuLimit;
        if (obj) {
            return (Number(obj.used) / Number(obj.max) * 100).toFixed(2);
        }
        return '0';
    }
    public get netPer(): string {
        const obj = this.control.accountControl.accountInfo.netLimit;
        if (obj) {
            return (Number(obj.used) / Number(obj.max) * 100).toFixed(2);
        }
        return '0';
    }

    public getResultClass(): any {
        const resultClass: any = {show: true};
        switch (this.control.game.result) {
            case BlackjackResult.Win:
                resultClass.win = true;
                break;
            case BlackjackResult.Lose:
                resultClass.lose = true;
                break;
            case BlackjackResult.Push:
                resultClass.draw = true;
                break;
            case BlackjackResult.Surrender:
                resultClass.sur = true;
                break;
            default:
                resultClass.show = false;
                break;
        }
        return resultClass;
    }

    public musicControl(): void {
        this.musicOn = !this.musicOn;
        const $bgm: any = document.getElementById('bgm');
        if (this.musicOn) {
            $bgm!.play();
        } else {
            $bgm!.pause();
        }
    }

    public copys(): void {
        this.copySuc = true;
        this.tipsTxt = this.langCn ? '复制成功！' : 'Copy succeed!';
        setTimeout(() => {
            this.copySuc = false;
            this.tipsTxt = '';
        }, 2000);
    }

    public beforeDestroy(): void {
        this.control.destroy();
    }

}

