import {EOS} from '../../eos/eos';
import {RedBlackBetType, RedBlackGame} from './redBlackGameTable';
import {RoundBasedGameController} from '@/modules/contract/roundBasedGame/roundBasedGameController';
import {RedBlackContract} from '@/modules/contract/redblack/redBlackContract';


export class RedBlackController extends RoundBasedGameController<RedBlackBetType> {
    public readonly contract: RedBlackContract;
    public game: RedBlackGame = new RedBlackGame(undefined);
    public betType: RedBlackBetType = RedBlackBetType.Unknown;

    public constructor(eos: EOS) {
        super(eos);
        this.contract = new RedBlackContract(this.house, this.typeToString);
    }

    public typeToString(result: RedBlackBetType): string {
        switch (result) {
            case RedBlackBetType.RedWin:
                return 'Red Win';
            case RedBlackBetType.BlackWin:
                return 'Black Win';
            case RedBlackBetType.Pair:
                return 'Pair';
            case RedBlackBetType.Straight:
                return 'Straight';
            case RedBlackBetType.Flush:
                return 'Flush';
            case RedBlackBetType.StraightFlush:
                return 'Straight Flush';
            case RedBlackBetType.ThreeOfAKind:
                return 'Three-of-a-Kind';
            default:
                return '---';
        }
    }

    public getResultString(): string {
        return this.typeToString(this.game.result);
    }

    public getRedHandTypeString(): string {
        return this.typeToString(this.game.redHandType);
    }

    public getBlackHandTypeString(): string {
        return this.typeToString(this.game.blackHandType);
    }
}
