import {Table} from '@/modules/eos/table';
import {EOS} from '@/modules/eos/eos';

export class GameResult<T> {
    public readonly id: number;
    public readonly gameId: number;
    public readonly result: T[];

    constructor(value: any) {
        this.id = value.id;
        this.gameId = value.game_id;
        this.result = [];

        for (let i = 0; i < 8; i++) {
            const flag: number = 1 << i;
            if ((value.result & flag) !== 0) {
                this.result.push(flag as any as T);
            }
        }
    }
}

export class GameResultTable<T> extends Table<GameResult<T>> {
    constructor(eos: EOS, contract: string) {
        super(eos, 'results', 'id', contract);
    }

    protected convert(row: any[]): Array<GameResult<T>> {
        return row.map((value) => new GameResult<T>(value)).sort((a, b) => a.gameId - b.gameId);
    }
}
