

































import Progress from '@/components/progress.vue';
import Loading from '@/components/loading.vue';
import {Vue, Component} from 'vue-property-decorator';
import moment from 'moment';
@Component({
    props: {
        data: {
            type: Array,
            required: true,
        },
        loading: {
            type: Boolean,
            required: true,
        },
        lang: {
            type: Boolean,
            required: false,
        },
    },
    filters: {
        fmt(t: any): string {
            if (!t) {
                return '';
            }
            t = Number(t) / 1000;
            return moment(new Date(t)).format('YYYY-MM-DD HH:mm:ss');
        },
    },
    components: {
        Progress,
        Loading,
    },
})
export default class BlackjackHistoryTableH5 extends Vue {
}
