import {EOS} from '../../eos/eos';
import {BaccaratBetType, BaccaratGame} from './baccaratGameTable';
import {BaccaratContract} from './baccaratContract';
import {RoundBasedGameController} from '@/modules/contract/roundBasedGame/roundBasedGameController';

export class BaccaratController extends RoundBasedGameController<BaccaratBetType> {
    public readonly contract: BaccaratContract;
    public game: BaccaratGame = new BaccaratGame(undefined);
    public betType: BaccaratBetType = BaccaratBetType.Unknown;

    public constructor(eos: EOS) {
        super(eos);
        this.contract = new BaccaratContract(this.house, this.typeToString);
    }

    public getResultString(): string {
        return this.typeToString(this.game.result);
    }

    public typeToString(result: BaccaratBetType): string {
        switch (result) {
            case BaccaratBetType.BankerWin:
                return 'Banker Win';
            case BaccaratBetType.PlayerWin:
                return 'Player Win';
            case BaccaratBetType.Tie:
                return 'Tie';
            case BaccaratBetType.BankerPair:
                return 'Banker Pair';
            case BaccaratBetType.PlayerPair:
                return 'Player Pair';
            default:
                return '';
        }
    }
}
