import {Asset} from '../../eos/asset';
import {Table} from '@/modules/eos/table';
import {EOS} from '@/modules/eos/eos';

export class Bet<T> {
    public readonly id: number;
    public readonly gameId: number;
    public readonly player: string;
    public readonly referer: string;
    public readonly bet: Asset;
    public readonly betType: T;
    public readonly betString: string;

    public constructor(value: any, converter: (_: T) => string) {
        this.id = value.id;
        this.gameId = value.game_id;

        this.player = value.player;
        this.referer = value.referer;
        this.bet = Asset.fromString(value.bet);
        this.betType = value.bet_type;
        this.betString = converter(this.betType);
    }
}

export class BetTable<T> extends Table<Bet<T>> {
    constructor(eos: EOS,
                contract: string,
                private converter: (_: T) => string) {
        super(eos, 'bets', 'id', contract);
    }

    protected convert(row: any[]): Array<Bet<T>> {
        return row.map((value) => new Bet(value, this.converter));
    }
}
