import {BlackjackHistoryRow} from './blackjackHistoryTable';
import {BlackjackGame} from './blackjackGameTable';
import {Asset} from '../../eos/asset';
import {Token} from '../../eos/token';
import {EOS} from '../../eos/eos';
import {BlackjackContract} from './blackjackContract';
import {BlackjackResult, BlackJackStatus} from './blackjackGame';
import {isEmptyString, isPresent} from '../../utils/commonFunctions';
import {TransactionReceipt} from '../../eos/transactionReceipt';
import {GameController} from '@/modules/contract/gameController';
import {Card} from '@/modules/contract/cards';

export enum LoadingStatus {
    Initializing, NotLoading, Shuffling, Standing, Hitting,
}

class MockGame {
    public constructor(public readonly playerCards: Card[],
                       public readonly bankerCards: Card[],
                       public readonly result: BlackjackResult) {
    }
}

export class BlackjackController extends GameController {
    public normalTicker: any = undefined;
    public myGameTicker: any = undefined;
    public bankCardTicker: any = undefined;
    public gameGlobals: any = undefined;
    public history: BlackjackHistoryRow[] = [];
    public historyLoading: boolean = true;
    public isFirstLoad: boolean = true;
    public game: BlackjackGame = new BlackjackGame(undefined);

    public loadingStatus: LoadingStatus = LoadingStatus.Initializing;
    public readonly contract: BlackjackContract;

    public constructor(eos: EOS) {
        super(eos);
        this.contract = new BlackjackContract(this.house);
    }

    public async initialize(): Promise<any> {
        this.startAllTick();
        this.startGetMyGame(0);
    }

    public hasStartGame(): boolean {
        return this.game.id > 0 && this.game.status === BlackJackStatus.Active;
    }

    public destroy(): void {
        this.stopAllTick();
    }

    public async loadMyGame(): Promise<any> {
        if (!this.eos.loggedIn()) {
            this.loadingStatus = LoadingStatus.NotLoading;
            throw new Error('Not logged in');
        }

        const game: BlackjackGame | undefined = await this.contract.gameTable.getById(this.eos.account.name);
        if (isPresent(game)) {
            if ((game!.status === this.game.status && game!.playerCards.length === this.game.playerCards.length) ||
                    game!.status === BlackJackStatus.Stood ||
                    game!.playerCards.length === 0) {
                throw new Error('Waiting for more game update');
            }
            if (game!.status === BlackJackStatus.Close) {
                if (!this.isFirstLoad) {
                    this.animateBankCards(game!);
                }
            } else {
                this.game = game!;
                this.loadingStatus = LoadingStatus.NotLoading;
            }
            this.isFirstLoad = false;
        } else {
            this.loadingStatus = LoadingStatus.NotLoading;
        }
    }

    public animateBankCards(game: BlackjackGame) {
        if (this.game.resultUpdate(game)) {
            if (isPresent(this.bankCardTicker)) {
                clearTimeout(this.bankCardTicker);
            }
            this.bankCardTicker = setTimeout(async () => {
                this.animateBankCards(game);
            }, 1000);
        } else {
            this.game = game;
            this.loadingStatus = LoadingStatus.NotLoading;
        }
    }

    public async loadHistory(): Promise<any> {
        this.history = await this.contract.historyTable.readAll();
        this.historyLoading = false;
    }

    public reset(): void {
        this.game  = new BlackjackGame(undefined);
    }

    public async startGetMyGame(delay: number = 2000, txId: string = ''): Promise<any> {
        if (!isEmptyString(txId)) {
            await this.eos.waitUntilImmutable(txId);
        }

        this.stopGetMyGame();
        this.myGameTicker = setTimeout(async () => {
            try {
                await this.loadMyGame();
                this.stopGetMyGame();

                this.loadingStatus = LoadingStatus.NotLoading;
                this.betControl.finishBet(-1);
            } catch (error) {
                this.startGetMyGame(500, '');
            }
        }, delay);
    }

    public stopGetMyGame() {
        if (this.myGameTicker !== null) {
            clearTimeout(this.myGameTicker);
            this.myGameTicker = null;
        }
    }

    public startAllTick() {
        this.stopAllTick();

        this.loadHistory();

        this.normalTicker = setTimeout(() => {
            this.startAllTick();
        }, 3000);
    }

    public stopAllTick() {
        if (this.normalTicker !== null) {
            clearTimeout(this.normalTicker);
            this.normalTicker = null;
        }
    }

    public async makeAction(action: () => Promise<TransactionReceipt>, status: LoadingStatus): Promise<any> {
        try {
            this.loadingStatus = status;

            const receipt: TransactionReceipt = await action();
            this.startGetMyGame(0, receipt.id);
            return receipt;
        } catch (error) {
            this.loadingStatus = LoadingStatus.NotLoading;
        }
    }

    public hit(): Promise<any> {
        return this.makeAction(() => this.contract.hit(), LoadingStatus.Hitting);
    }

    public async stand(): Promise<any> {
        return this.makeAction(() => this.contract.stand(), LoadingStatus.Standing);
    }

    public async surrender(): Promise<any> {
        return this.makeAction(() => this.contract.surrender(), LoadingStatus.Standing);
    }

    public isWaitingForAction(): boolean {
        return this.loadingStatus !== LoadingStatus.NotLoading;
    }

    public isStanding(): boolean {
        return this.loadingStatus === LoadingStatus.Standing;
    }

    public isHitting(): boolean {
        return this.loadingStatus === LoadingStatus.Hitting;
    }

    public isShuffling(): boolean {
        return this.loadingStatus === LoadingStatus.Shuffling;
    }

    public betText(): string {
        return 'Play';
    }

    public canMakeBet(): boolean {
        return this.game.status !== BlackJackStatus.Active;
    }

    public async makeBet(amount: number): Promise<any> {
        const receipt: TransactionReceipt =
                await this.contract.play(new Asset(amount, Token.EOS));

        this.reset();
        this.startGetMyGame(0, receipt.id);
    }
}
