import {Constants} from '@/modules/eos/constants';
import {Network} from '@/modules/eos/network';
import {HouseContract} from '@/modules/contract/house/houseContract';
import {RoundBasedGameContract} from '@/modules/contract/roundBasedGame/roundBasedGameContract';
import {RedBlackBetType, RedBlackGameTable} from '@/modules/contract/redblack/redBlackGameTable';


export class RedBlackContract extends RoundBasedGameContract<RedBlackBetType> {
    public static readonly CONTRACT = Constants.NETWORK === Network.LOCALHOST ? 'redblack' : 'godappredbla';
    public static readonly ID: number = 4;

    public readonly gameTable: RedBlackGameTable;

    public constructor(house: HouseContract, converter: (_: RedBlackBetType) => string) {
        super(RedBlackContract.CONTRACT, house, RedBlackContract.ID, converter);
        this.gameTable = new RedBlackGameTable(this.eos, this.name);
    }
}
